export class Stack {
    constructor() {
        this._elements = [];
    }
    push(item) {
        this._elements.push(item);
    }
    pop() {
        return this._elements.pop();
    }
    top() {
        return this._elements.at(-1);
    }
    isEmpty() {
        return this._elements.length === 0;
    }
    size() {
        return this._elements.length;
    }
}
