export class Queue {
    constructor() {
        this._elements = {};
        this._head = 0;
        this._tail = 0;
    }
    enqueue(item) {
        this._elements[this._tail] = item;
        this._tail++;
    }
    dequeue() {
        if (this._tail === this._head)
            return undefined;
        const element = this._elements[this._head];
        delete this._elements[this._head];
        this._head++;
        return element;
    }
    peek() {
        if (this.isEmpty())
            return undefined;
        return this._elements[this._head];
    }
    isEmpty() {
        return this._head === this._tail;
    }
}
